@font-face {
  font-family: 'signature';
  src: url('fonts/Thesignature.otf') format('opentype');
}

nav a {
  font-family: montserrat, Sans-serif;
  font-size: 17px!important;
}

.navbar-collapse ul {
  position: relative;
  top: 6px;
}
ul .inicio:hover{
  border-bottom:2px solid #d8ac84;
  transition: 300ms;
}
ul .comoc:hover{
  border-bottom:2px solid #d8ac84;
  transition: 300ms;
}
ul .productos:hover{
  border-bottom:2px solid #d8ac84;
  transition: 300ms;
}

.navbar-collapse ul li {
  border-bottom:2px solid #d8ad8400;
}

@media screen and (min-width: 1368px){
  .box{
    max-width: 1200px !important;
    margin: 0 auto 0 auto;
  }
}

.logoj {
  display: block;
  width: 150px;
  margin: 0 auto;
}

.navlogo {
  background-color: rgb(253, 253, 253);
  box-shadow: rgba(0, 0, 0, 0.3) 0px 4px 7px -5px;
  margin-bottom: 20px;
}

.dbg {
  width: 240px;
  position: absolute;
  top: 0px;
}

.division {
  width: 300px;
}

.tortas {
  display: flex;
  font-family: signature, Sans-serif;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-top: 40px;
}
.tortas h4 {
  font-size: 120px;
}

.fotoc:hover img{
  filter: brightness(70%);
  transition: 200ms;
  cursor: pointer;
}

.placetortas div div{
  background-color: rgb(253, 253, 253);
  border-radius: 20px;
  padding: 15px 15px;
  margin-bottom: auto;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 0px 20px 2px;
  text-align: center;
}
.placetortas div div p {
  margin: 5px 0 0 0!important;
  font-size: 20px;
  font-family: montserrat, sans-serif;
}
.placetortas div img {
  width: 250px;
}

.containertortas {
  box-shadow: rgba(0, 0, 0, 0.3) 0px 4px 7px -5px;
  max-width: 100%!important;
  margin-left: 0;
  margin-right: 0;
}

.textoinicio div {
  font-size: 20px;
  font-family: montserrat, sans-serif;
}
.textoinicio div img {
  width: 50px;
  background-color: #f8d3d6;
  padding: 10px;
  border-radius: 30px;
}
.textoinicio div p {
  margin: auto auto auto 10px!important;
}

.fixednav {
  position: fixed;
  top: 0;
  z-index: 3;
  width: 100%;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 4px 7px -5px;
  transition: 300ms;
}

.jsfooter{
  background-color: #f8d3d6;
  margin-bottom: 0;
}